@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .container {
    width: 85%;
    margin: 0 auto;
  }
  a {
    text-decoration: none;
    font-family: "Poppins", sans-serif;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  p,
  span {
    font-family: "Poppins", sans-serif;
  }
  h1 {
    font-style: normal;
    font-weight: 600;
    font-size: 45px;
    line-height: 60px;
    display: flex;
    align-items: center;
    color: #074269;
    margin: 8px 0;
  }
  h2 {
    font-style: normal;
    font-weight: 600;
    font-size: 40px;
    line-height: 60px;
    display: flex;
    align-items: center;
    color: #074269;
    margin: 8px 0;
  }
  h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 42px;
    display: flex;
    align-items: center;
    color: #074269;
  }
  div {
    outline: none;
  }
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 36px;
    display: flex;
    align-items: center;
    color: #708ca0;
    margin: 0;
  }
  .btn {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #074269;
    padding: 8px 33px;
    background: #ffdd28;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    outline: none;
    &.emptyBtn {
      font-weight: normal !important;
      color: #708ca0 !important;
      background-color: #f3f5f7 !important;
    }
    &.blueBtn {
      color: #ffffff !important;
      background-color: #68c0ff !important;
    }
    span {
      margin-left: 8px;
      width: auto;
      height: auto;
      margin-top: 4px;
      img {
        width: auto;
        height: auto;
      }
    }
  }
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input:-webkit-autofill {
    box-shadow: 0 0 0px 1000px #fff inset;
    -webkit-text-fill-color: #333;
    transition: background-color 5000s ease-in-out 0s;
  }
  input:-webkit-autofill:focus {
    box-shadow: 0 0 0px 1000px #fff inset;
    transition: background-color 5000s ease-in-out 0s;
    -webkit-text-fill-color: #333;
  }
  .textInput {
    position: relative;
    padding: 0;
    display: block;
    .error-text {
      text-align: start;
      color: #fd6e6a !important;
      font-size: 12px !important;
      line-height: 14px !important;
      margin: 0 !important;
      padding-bottom: 5px;
      padding-left: 5px;
    }
    label {
      position: absolute;
      top: 19px;
      left: 15px;
      padding: 0;
      color: #c4c4c4;
      pointer-events: none;
      transition: 0.5s;
      z-index: 10;
      margin-bottom: 0;
      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 200;
      font-size: 16px;
      line-height: 18px;
      span {
        color: #32a9ff;
      }
    }
    .input {
      width: 100%;
      height: 40px;
      border: 1px solid #e5e5e5;
      outline: none;
      box-sizing: border-box;
      border-radius: 10px;
      margin: 8px 0;
      padding: 0 15px;
      font-size: 16px;
      line-height: 18px;
      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 200;
      color: #333;
      outline: none;

      &::-webkit-input-placeholder,
      &:-moz-placeholder,
      &:-ms-input-placeholder {
        color: #c4c4c4;
        font-size: 14px;
      }
      // &:not([value=""]) {
      //   border-color: #ffdd28;
      //   color: #ffdd28 !important;
      // }

      &:focus ~ label,
      &:not([value=""]) ~ label {
        top: 4px !important;
        left: 8px !important;
        font-size: 8px;
        line-height: 12px;
        background-color: white;
        padding-left: 5px;
        padding-right: 5px;
        height: none;
      }

      &:focus ~ label {
        color: #32a9ff;
      }

      &:focus,
      &:valid ~ label,
      &:not([value=""]) ~ label {
        outline: none;
        border-color: #32a9ff;
        box-shadow: none;
      }
    }

    textarea.textfield {
      height: 150px;
      width: 100%;
      outline: none;
      border: 1px solid #e5e5e5;
      outline: none;
      box-sizing: border-box;
      border-radius: 10px;
      margin: 8px 0;
      padding: 15px;
      font-size: 16px;
      line-height: 18px;
      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 200;
      color: #333;
      resize: none;

      &:focus ~ label {
        color: #32a9ff !important;
      }

      &:focus ~ label {
        color: #32a9ff;
        top: 4px;
        left: 8px;
        font-size: 8px;
        line-height: 12px;
        background-color: white;
        padding-left: 5px;
        padding-right: 5px;
        height: none;
      }

      &:focus,
      &:valid ~ label,
      &:not([value=""]) ~ label {
        outline: none;
        border-color: #32a9ff;
        box-shadow: none;
      }

      &.has-value {
        border-color: #32a9ff;
        color: #667c8a !important;
      }
      &.has-value:focus {
        border-color: #32a9ff;
        color: #667c8a !important;
      }

      &.has-value ~ label {
        top: 4px;
        left: 8px;
        font-size: 8px;
        background-color: white;
        padding-left: 5px;
        padding-right: 5px;
        // color: #919eab;
        outline: none;
        border-color: #32a9ff;
        box-shadow: none;
        color: #32a9ff;
      }
      &.has-value:focus ~ label {
        color: #32a9ff;
      }
    }
    &.errorInput {
      label {
        color: #fd6e6a !important;
      }
      .input {
        border-color: #fd6e6a !important;
      }
    }
  }
  .react-responsive-modal-modal {
    border-radius: 10px !important;
    padding: 0 !important;
    button{
      outline: none;
    }
    .popup {
      // max-width: 700px;
      padding: 35px 65px;
      .container {
        width: 100%;
        margin: 0;
        h2 {
          font-weight: 600;
          font-size: 20px;
          line-height: 30px;
          color: #074269;
          margin-bottom: 18px;
        }
        h1 {
          font-weight: normal;
          font-size: 18px;
          line-height: 27px;
          margin: 12px 0 8px;
        }
        .serviceCheck {
          display: flex;
          flex-flow: row wrap;
          padding-bottom: 12px;
          width: 100%;
          .inputCheck {
            width: max-content;
            margin-right: 30px;
            cursor: pointer;
            &:last-child{
              margin-right: 0;
            }
            .input[type="checkbox"] {
              cursor: pointer;
              margin-right: 12px;
            }
            label {
              font-weight: normal;
              font-size: 16px;
              line-height: 24px;
              color: #9dadbe;
              cursor: pointer;
            }
          }
        }
        .bottom {
          display: flex;
          align-items: center;
          justify-content: center;
          padding-top: 14px;
          .emptyBtn {
            margin-right: 15px;
          }
        }
      }
    }
  }
}

code {
  font-family: "Poppins", sans-serif;
}
